<script>
import axios from 'axios'
export default {
  data() {
    return {
      slots: [
        { name: "A1", booked: false },
        { name: "A2", booked: false },
        { name: "A3", booked: false },
        { name: "B1", booked: false },
        { name: "B2", booked: false },
        { name: "B3", booked: false },
      ],
      showForm: false,
      showSuccessMessage: false,
      booking: {
        slotName: "",
        ownerName: "",
        vehicleType: "2w",
        vehicleModel: "",
        vehicleNumber: "",
        entryTime: "",
        exitTime: "",
        parkingFee: "",
      },
    };
  },
  methods: {
    bookSlot(slot) {
      if (!slot.booked) {
        this.booking.slotName = slot.name;
        this.showForm = true;
      }
    },
    calculateParkingFee(entryTime, exitTime, vehicleType) {
      const oneHourInMs = 60 * 60 * 1000;
      const entryDate = new Date(entryTime);
      const exitDate = new Date(exitTime);
      const timeDiffInMs = exitDate - entryDate;
      const timeDiffInHours = timeDiffInMs / oneHourInMs;
      let parkingFee = 0;
      switch (vehicleType) {
        case "2w":
          parkingFee = timeDiffInHours * 10;
          break;
        case "3w":
          parkingFee = timeDiffInHours * 15;
          break;
        case "4w":
          parkingFee = timeDiffInHours * 20;
          break;
      }
      return parkingFee.toFixed(2);
    },
    async submitBookingForm() {
      try {
        const response = await axios.post("http://127.0.0.1:3000/buynow", {
