          vehicleType: this.booking.vehicleType,
          vehicleModel: this.booking.vehicleModel,
          vehicleNumber: this.booking.vehicleNumber,
          entryTime: this.booking.entryTime,
          exitTime: this.booking.exitTime,
          parkingFee: this.booking.parkingFee,
        });
        console.log(response.data);
        alert("Car is Booked .");
        if (response.status === 201) {
          alert("Car is Booked Successfully");
        }

        // Display success message to user or redirect to success page
      } catch (error) {
        console.error(error);
        alert("Car is not avaliable now");
        // Display error message to user or redirect to error page
      }
    },
    resetBookingForm() {
      this.booking = {
