        ownerName: "",
        vehicleType: "2w",
        vehicleModel: "",
        vehicleNumber: "",
        entryTime: "",
        exitTime: "",
        parkingFee: "",
      };
      this.showSuccessMessage = false;
    },
  },
};
</script>

