<template>
<div>


  <div class="container mt-5">
    <h2 class="text-center mb-5">Parking Reservation System</h2>
    <div class="row">
      <div class="col-md-8">
        <div class="row parking-slots">
          <div
            class="col-md-3 mb-3"
            v-for="slot in slots"
            :key="slot.id"
            :class="{
              'parking-slot': true,
              available: slot.available,
              booked: !slot.available,
            }"
            @click="selectSlot(slot)"
          >
            <span class="text-white" v-if="slot.available"
              >Slot {{ slot.id }} Available</span
            >
            <span class="text-white" v-else>Booked</span>
          </div>
        </div>
      </div>
      <div class="col-md-4">
        <div v-if="selectedSlot" class="booking-form">
          <h3 class="text-center mb-4">Book Slot {{ selectedSlot.id }}</h3>
          <form @submit.prevent="submitForm">
            <div class="form-group">
              <label for="ownerName">Owner Name:</label>
              <input
                type="text"
                class="form-control"
                id="ownerName"
                v-model="form.ownerName"
                required
              />
            </div>
            <div class="form-group">
              <label for="vehicleModel">Vehicle Model:</label>
              <input
                type="text"
                class="form-control"
                id="vehicleModel"
                v-model="form.vehicleModel"
                required
              />
            </div>
            <div class="form-group">
              <label for="vehicleType">Vehicle Type:</label>
              <select
                class="form-control"
                id="vehicleType"
                v-model="form.vehicleType"
                required
              >
                <option value="">Select vehicle type</option>
                <option value="2-wheeler">2-Wheeler</option>
                <option value="3-wheeler">3-Wheeler</option>
                <option value="4-wheeler">4-Wheeler</option>
              </select>
            </div>
            <div class="form-group">
              <label for="vehicleNumber">Vehicle Number:</label>
              <input
                type="text"
                class="form-control"
                id="vehicleNumber"
                v-model="form.vehicleNumber"
                required
              />
            </div>
            <div class="form-group">
              <label for="entryTime">Entry Datetime:</label>
              <input
                type="datetime-local"
                class="form-control"
                id="entryTime"
                v-model="form.entryTime"
                required
              />
            </div>
            <div class="form-group">
              <label for="exitTime">Exit Datetime:</label>
              <input
                type="datetime-local"
                class="form-control"
                id="exitTime"
                v-model="form.exitTime"
                required
              />
            </div>
            <div class="form-group">
              <label for="fees">Fees:</label>
              <span v-if="form.vehicleType === '2-wheeler'" ref="spanValue">{{
                calculateFees(5)
              }}</span>
              <span
                v-else-if="form.vehicleType === '3-wheeler'"
                ref="spanValue"
                >{{ calculateFees(10) }}</span
              >
              <span
                v-else-if="form.vehicleType === '4-wheeler'"
                ref="spanValue"
                >{{ calculateFees(10) }}</span
              >
            </div>
            <button type="submit" class="btn btn-primary">Book Slot</button>
          </form>
        </div>
      </div>
    </div>
  </div>
  <footerPage class="footer1"  />
  </div>
</template>

<script>
import axios from "axios";
import jsPDF from "jspdf";
import footerPage from "./footerPage.vue";

export default {
  components: {
    footerPage,
  },
  data() {
    return {
      slots: [
        { id: 1, available: true },
        { id: 2, available: true },
        { id: 3, available: true },
        { id: 4, available: true },
        { id: 5, available: true },
        { id: 6, available: true },
        { id: 7, available: true },
        { id: 8, available: true },
        { id: 9, available: true },
        { id: 10, available: true },
      ],
      selectedSlot: 1,
      form: {
        ownerName: "",
        vehicleModel: "",
        vehicleType: "",
        vehicleNumber: "",
        entryTime: "",
        exitTime: "",
        fees: 0,
      },
    };
  },
  methods: {
    selectSlot(slot) {
      if (slot.available) {
        this.selectedSlot = slot;
      }
    },
    generateTransactionId() {
      const chars =
        "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
      const charsLength = chars.length;
      let transactionId = "";
      for (let i = 0; i < 13; i++) {
        const randomIndex = Math.floor(Math.random() * charsLength);
        transactionId += chars.charAt(randomIndex);
      }
      return transactionId;
    },
    calculateFees(rate) {
      const entryTime = new Date(this.form.entryTime);
      const exitTime = new Date(this.form.exitTime);
      const diffTime = Math.abs(exitTime - entryTime);
      const diffHours = Math.ceil(diffTime / (1000 * 60 * 60));
      return diffHours * rate;
    },
    resetForm() {
      this.form.ownerName = "";
      this.form.vehicleModel = "";
      this.form.vehicleType = "";
      this.form.vehicleNumber = "";
      this.form.entryTime = "";
      this.form.exitTime = "";
      this.form.fees = 0;
    },
    generateReceipt() {
  const transactionId = this.generateTransactionId();
  const doc = new jsPDF();

  // Define color scheme
  const primaryColor = [30, 67, 137];
  const lightGray = [224, 224, 224];

  // Set up fonts
  doc.addFont('Helvetica-Bold.ttf', 'helvetica-bold', 'bold');
  doc.addFont('Helvetica.ttf', 'helvetica', 'normal');
  doc.setFont('helvetica');

  // Add ParkMyRide branding
  doc.setFillColor(...primaryColor);
  doc.rect(0, 0, 210, 40, 'F');
  doc.setTextColor(255, 255, 255);
  doc.setFontSize(24);
  doc.setFont('helvetica-bold');
  doc.text('ParkMyRide', 105, 25, { align: 'center' });
  doc.setFontSize(12);
  doc.text('Your one-stop parking reservation system', 105, 35, { align: 'center' });
  doc.setTextColor(0, 0, 0);

  // Add transaction details table
  doc.setFillColor(...lightGray);
  doc.rect(20, 70, 170, 110, 'F');
  doc.setFontSize(16);
  doc.setFont('helvetica-bold');
  doc.text('Parking Receipt', 105, 80, { align: 'center' });
  doc.setDrawColor(...primaryColor);
  doc.setLineWidth(0.5);
  doc.line(20, 90, 190, 90);
  doc.setFont('helvetica');
  doc.setFontSize(12);
  doc.text(`Transaction ID: ${transactionId}`, 25, 105);
  doc.text(`Owner Name: ${this.form.ownerName}`, 25, 115);
  doc.text(`Vehicle Model: ${this.form.vehicleModel}`, 25, 125);
  doc.text(`Vehicle Number: ${this.form.vehicleNumber}`, 25, 135);
  doc.text(`Entry Time: ${this.form.entryTime}`, 25, 145);
  doc.text(`Exit Time: ${this.form.exitTime}`, 25, 155);
  doc.setFont('helvetica-bold');
  doc.text(`Fees: Rs. ${this.$refs.spanValue.textContent}`, 25, 170);

  // Add contact information
  doc.setFontSize(10);
  doc.setFont('helvetica');
  doc.text('Contact us:', 20, 195);
  doc.text('Phone: +91-1234567890', 20, 200);
  doc.text('Email: contact@parkmyride.com', 20, 205);

  // Add thank you message
  doc.setFontSize(14);
  doc.setFont('helvetica-bold');
  doc.text('Thank you for using ParkMyRide!', 105, 240, { align: 'center' });

  // Save the PDF file with a custom filename
  doc.save(`ParkMyRide_ParkingReceipt_${transactionId}.pdf`);
}
,

    async submitForm() {
      try {
        const response = await axios.post("http://127.0.0.1:3000/buynow", {
          ownerName: this.form.ownerName,
          slotName: this.selectedSlot.id,
          vehicleType: this.form.vehicleType,
          vehicleModel: this.form.vehicleModel,
          vehicleNumber: this.form.vehicleNumber,
          entryTime: this.form.entryTime,
          exitTime: this.form.exitTime,
          parkingFee: this.$refs.spanValue.textContent,
        });
        console.log(response.data);
        if (response.status === 201) {
          const slotIndex = this.slots.findIndex(
            (slot) => slot.id === this.selectedSlot.id
          );
          if (slotIndex >= 0) {
            this.slots[slotIndex].available = false;
            this.selectedSlot = 1;
            alert(
              `Booking Successful!\nOwner Name: ${this.form.ownerName}\nCar Number: ${this.form.vehicleNumber}\nBooking Fees: ${this.$refs.spanValue.textContent}`
            );
          }
        }
        this.generateReceipt();
        this.resetForm();
        // Display success message to user or redirect to success page
      } catch (error) {
        console.error(error);
        alert("Slot is not avaliable now");
        // Display error message to user or redirect to error page
      }
    },
  },
};
</script>

