<template>
  <div class="chatbot-container">
    <header>Talk With Us</header>
    <section class="main">
      <div class="chatarea-main">
        <div class="chatarea-outer">
          <div
            v-for="(message, index) in chatMessages"
            :key="index"
            class="chatarea-inner"
            :class="message.sender"
          >
            {{ message.text }}
          </div>
        </div>
      </div>
      <div class="chatbot-area">
        <img src="../assets/chatbot/chat.svg" />
      </div>
      <button id="mic" @click="activateMic">
        <img src="../assets/mic.gif" >
      </button>
    </section>
  </div>
</template>

<script>
export default {
  data() {
    return {
      chatMessages: [],
      intro: ["Welcome to ParkMyRide! How can I assist you?"],
        help: ["How may I help you with your parking reservation?"],
        greetings: [
          "I'm doing well, thank you. How can I assist you with your parking reservation?",
          "I'm fine, thanks for asking. How can I assist you with your parking reservation?",
        ],
        info: [
          "I can help you with parking reservations, pricing, and availability.",
          "You can make a reservation, check availability, and view prices with me!",
        ],
        reservation: [
          "Sure, what date and time would you like to reserve a spot?",
          "I can help you reserve a spot. When do you need it?",
        ],
        confirmation: [
          "Your reservation is confirmed! Thank you for choosing ParkMyRide!",
          "Great, your parking spot is reserved! Thank you for choosing ParkMyRide!",
        ],
        thanks: [
          "You're welcome!",
          "No problem!",
          "It's my pleasure!",
          "Anytime!",
        ],
        closing: ["Okay, have a great day!", "Goodbye!"],
    };
  },
  methods: {
    showUserMsg(userMsg) {
      this.chatMessages.push({ text: userMsg, sender: "user" });
    },
    showChatbotMsg(chatbotMsg) {
      this.chatMessages.push({ text: chatbotMsg, sender: "chatbot" });
    },
    chatbotVoice(message) {
      const speech = new SpeechSynthesisUtterance();
      speech.text = "This is test message";
      let response = "";
      if (message.includes("hello")) {
        response = this.intro[Math.floor(Math.random() * this.intro.length)];
      } else if (message.includes("hi")) {
        response = this.help[Math.floor(Math.random() * this.help.length)];
      } else if (message.includes("how are you")) {
        response =
          this.greetings[Math.floor(Math.random() * this.greetings.length)];
      } else if (message.includes("tell me something about you")) {
        response =
          this.info[Math.floor(Math.random() * this.info.length)];
      } else if (message.includes("reserve")) {
        response = this.reservation[Math.floor(Math.random() * this.reservation.length)];
      } else if (message.includes("confirm")) {
        response = this.confirmation[Math.floor(Math.random() * this.confirmation.length)];
      } else if (message.includes("thanks")) {
        response =
          this.thanks[Math.floor(Math.random() * this.thanks.length)];
      }
      else if (message.includes("bye")) {
        response =
          this.closing[Math.floor(Math.random() * this.closing.length)];
      }
      speech.text =
        response ||
        "I'm sorry, I didn't understand that. Can you please rephrase?";
      window.speechSynthesis.speak(speech);
      return response;
    },
    activateMic() {
      const { webkitSpeechRecognition } = window;
      const recognition = new webkitSpeechRecognition();
      recognition.lang = "en-US";
      recognition.start();
      recognition.onresult = (event) => {
        const speechResult = event.results[0][0].transcript;
        this.showUserMsg(speechResult);
        const chatbotResponse = this.chatbotVoice(speechResult);
        if (chatbotResponse) {
          this.showChatbotMsg(chatbotResponse);
        } else {
          setTimeout(() => {
            this.showChatbotMsg(
              "I'm sorry, I didn't understand that. Can you please rephrase?"
            );
          }, 1000);
        }
      };
      recognition.onend = () => {
        console.log("Speech recognition service disconnected");
      };
    },
  },
};
</script>

<style scoped>
@font-face {
  font-family: "Roboto-Regular";
  src: url("../assets/chatbot/Roboto-Regular.eot");
  src: local(""), url("../assets/chatbot/Roboto-Regular.woff") format("woff"),
    url("../assets/chatbot/Roboto-Regular.ttf") format("truetype"),
    url("../assets/chatbot/Roboto-Regular.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Flaticon";
  src: url("../assets/chatbot/Flaticon.eot");
  src: url("../assets/chatbot/Flaticon.eot?#iefix") format("embedded-opentype"),
    url("../assets/chatbot/Flaticon.woff2") format("woff2"),
    url("../assets/chatbot/Flaticon.woff") format("woff"),
    url("../assets/chatbot/Flaticon.ttf") format("truetype"),
    url("../assets/chatbot/Flaticon.svg#Flaticon") format("svg");
  font-weight: normal;
  font-style: normal;
}

@media screen and (-webkit-min-device-pixel-ratio: 0) {
  @font-face {
    font-family: "Flaticon";
    src: url("../assets/chatbot/Flaticon.svg#Flaticon") format("svg");
  }
}

[class^="flaticon-"]:before,
[class*=" flaticon-"]:before,
[class^="flaticon-"]:after,
[class*=" flaticon-"]:after {
  font-family: Flaticon;
  font-size: 30px;
  font-style: normal;
}

.flaticon-microphone:before {
  content: "\f100";
}

