.chatarea-inner.chatbot:before {
  content: "";
  position: absolute;
  border-left: 20px solid #466eb6;
  border-top: 20px solid transparent;
  border-bottom: 0px solid transparent;
  bottom: 0;
  right: -20px;
}

.chatbot-area .chatarea-inner {
  float: left;
}
#mic {
  border: 2px solid #33CDC7;
}

#mic img{
  width: 40px;
}
</style>
