<script>
import axios from "axios";

import footerPage from "./footerPage.vue";

export default {
  components: {
    footerPage,
  },
  data() {
    return {
      name: "",
      email: "",
      phone: "",
      message: "",
    };
  },
  methods: {
    resetForm() {
      this.name = "";
      this.email = "";
      this.message = "";
    },
    async submitForm() {
      try {
        const response = await axios.post("http://127.0.0.1:3000/ContactUs", {
          name: this.name,
          email: this.email,
          message: this.message,
        });
        console.log(response.data);
        alert(
          "Thank you for contacting ParkMyRide!\nYour message has been received and we will respond to you as soon as possible."
        );
        this.resetForm();
        // Display success message to user or redirect to success page
      } catch (error) {
        console.error(error);
        alert("Server not avaliable now");
        // Display error message to user or redirect to error page
      }
    },
  },
};
</script>

